.player-stats-wrapper .filters {
    display: flex;
}

.country-filter,
.position-filter {
    margin-left: 1rem;
}

.player-stats-wrapper .table {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    width: fit-content;
}

.player-stats-wrapper .thead {
    display: flex;
    flex-direction: row;
}

.player-stats-wrapper .th. td {
    margin-left: 1rem;
}

.th:first-child,
.td:first-child {
    margin-left: 0;
}

.tr {
    display: flex;
    flex-direction: row;
    background: #d4e9fc;
    height: 1.75rem;
    align-items: center;
}

.table .name {
    width: 10rem;
    margin-left: 1rem;
}

.th.tp {
    cursor: pointer;
}

.table .tp {
    width: 4rem;
    text-align: center;
}

.table .team,
.table .position,
.table .country {
    width: 6rem;
    text-align: center;
}

.tr:nth-child(odd) {
    background: #f7f7f7;
}
