.view-selector {
    display: flex;
    flex-direction: row;
    margin-bottom: .5rem;
    margin-top: .5rem;
}

.view-type {
    cursor: pointer;
    margin-left: .5rem;
}

.view-type.selected {
    text-decoration: underline;
}

.select-tour {
    margin-bottom: .5rem;
    width: 10rem;
}

.tour-info {
    display: flex;
    flex-direction: row;
}

.tour-info .additional-stats {
    margin-left: 2rem;
}

.full-players-points .additional-stats {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
}

.full-players-points .additional-stats .points-by-country {
    margin-left: 1.5rem;
}

.tour-info .additional-stats .points-by-country {
    margin-top: 1rem;
}

.points-by-position .title,
.points-by-country .title {
    text-decoration: underline;
    margin-bottom: 0.5rem;
}

.position-points span,
.country-points span {
    width: 3rem;
    display: inline-block;
}

.position-points,
.country-points {
    cursor: default;
}
