.user-name {
    text-align: center;
    padding: 0.25rem;
}

.user-total {
    text-align: center;
    padding-bottom: 0.25rem;
}

.user-head {
    background: #d4e9fc;
}

.user-head.mobile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .25rem 0;
    margin-bottom: .5rem;
}

.user-head.mobile .user-total {
    padding: 0.25rem;
}

.user-body.mobile {
    width: 100%;
    display: none;
    flex-direction: column;
    justify-content: space-between;
}

.user-body.mobile.shown {
    display: flex;
    margin-bottom: .5rem;
}