.layout {
    display: flex;
    height: 100vh;
}

@media screen and (max-width: 640px) {
    .layout {
        flex-direction: column;
    }
}

.header {
    display: none;
    flex-direction: row-reverse;
    margin-bottom: 0.5rem;
}

.header.shown {
    display: flex;
}

.header-content {
    height: 30px;
    display: flex;
    align-items: center;
}

.sidebar {
    height: 100%;
    width: 18rem;
    background-color: #f7f7f7;
    border-right: solid 1px #e3e3e3;
    display: flex;
    flex-direction: column;
}

.sidebar.hidden {
    display: none;
}

.sidebar.mobile {
    position: absolute;
    z-index: 1;
}

.sidebar > * {
    padding-left: 2rem;
    padding-right: 2rem;
}

.sidebar nav {
    flex: 1;
    overflow: auto;
    padding-top: 1rem;
}

.sidebar nav a span {
    float: right;
    color: #eeb004;
}
.sidebar nav a.active span {
    color: inherit;
}

i {
    color: #818181;
}
.sidebar nav .active i {
    color: inherit;
}

.sidebar ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebar li {
    margin: 0.25rem 0;
}

.sidebar li:first-child {
    margin-top: 0;
}

.sidebar nav a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;

    white-space: pre;
    padding: 0.5rem;
    border-radius: 8px;
    color: inherit;
    text-decoration: none;
    gap: 1rem;
}

.sidebar nav a:hover {
    background: #e3e3e3;
}

.sidebar nav a.active {
    background: #d4e9fc;
    /*color: white;*/
}

.main {
    flex-grow: 1;
    overflow-y: scroll;
    position: relative;
    padding: 1rem;
}

.main.mobile {
    padding-top: 0.5rem;
}

ul li .active {
    text-decoration: underline;
}

ul li .non-active {
    text-decoration: none;
}

.show-menu {
    padding: 0.25rem;
    border: 1px solid black;
    border-radius: 2px;
    height: 20px;
    font-size: 14px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1rem;
    margin-top: 0.5rem;
}
